<template>
  <div class="referrals">
    <!-- 搜索框 -->
    <div class="serch">
      <van-search class="ipt" v-model="value" placeholder="请输入搜索关键词" />
    </div>
    <!-- 热门歌手 -->
    <div class="referrals-hot-singer">
      <h3>热门歌手</h3>
      <br />
      <ul class="hot-singer-list">
        <li
          class="hot-singer-item"
          v-for="(item, index) in hotSinger"
          :key="index"
        >
          <hotSinger :hotSinger="item" />
        </li>
      </ul>
    </div>
    <!-- 视频推荐 -->
    <div class="referrals-hot-mv">
      <h3>最新MV</h3>
      <ul class="hot-mv-list">
        <li class="hot-mv-item" v-for="(item, index) in mvList" :key="index">
          <newMv :mvList="item" />
        </li>
      </ul>
    </div>
    <!-- 情感 -->
    <div class="referrals-mv-paihang">
      <h3>MV排行</h3>
      <ul class="mv-paihang">
        <li
          class="paihang-item"
          v-for="(item, index) in mvPaiHang"
          :key="index"
        >
          <div>
            <span>{{ index + 1 }}</span>
          </div>
          <div class="mv-data">
            <h5 class="name">Mv:{{ item.name }}</h5>
            <h6 class="artis-name">歌手:{{ item.artistName }}</h6>
          </div>
          <img :src="item.cover" alt="" />
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
//歌手
import hotSinger from "../components/HotSinger.vue";
//MV
import newMv from "../components/NewMv.vue";
export default {
  name: "Referrals",
  components: {
    hotSinger,
    newMv,
  },
  data() {
    return {
      //热门歌手
      hotSinger: [],
      //最新MV
      mvList: [],
      //Mv排行
      mvPaiHang: [],
      value: "",
    };
  },
  methods: {
    //获取热门歌手 12
    async getHotSinger() {
      this.axios
        .get("top/artists", {
          params: {
            limit: 12,
          },
        })
        .then((res) => {
          if (res.code == 200) {
            this.hotSinger = res.artists;
          }
        });
    },
    //获取视频推荐
    async getMv() {
      this.axios
        .get("mv/exclusive/rcmd", { params: { limit: 10 } })
        .then((res) => {
          if (res.code == 200) {
            this.mvList = res.data;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //获取Mv排行
    async getMvPaiHang() {
      this.axios
        .get("top/mv", { params: { limit: 10 } })
        .then((res) => {
          if (res.code == 200) {
            this.mvPaiHang = res.data;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  created() {
    //获取热门歌手 12
    this.getHotSinger();
    //获取最新Mv
    this.getMv();
    //获取MV排行
    this.getMvPaiHang();
  },
};
</script>

<style lang="less" scoped>
.referrals {
  overflow: hidden;
  > div {
    padding: 5px 0 10px 0;
    background-color: white;
    margin-bottom: 10px;
  }
  > div > h3 {
    color: red;
    margin-left: 5px;
  }
}
.serch {
  position: fixed;
  width: 100%;
  left: 0;
  z-index: 999;
}
.referrals-hot-singer {
  margin-top: 70px;
  height: 350px;
  overflow: hidden;
  overflow: hidden;
  .hot-singer-list {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    height: 100%;
    .hot-singer-item {
      width: 22%;
    }
  }
}
.referrals-hot-mv {
  overflow-x: scroll;
  padding: 5px;
  background: white;
  .hot-mv-list {
    width: calc(100% * 5);
    overflow: hidden;
    height: 100%;
    display: flex;
  }
  .hot-mv-item {
    width: 200px;
  }
}
.referrals-mv-paihang {
  overflow: hidden;
  height: 350px;
  .mv-paihang {
    margin-top: 5px;
    height: 95%;
    overflow-y: scroll;
    padding: 0 0 20px;
  }
  .paihang-item {
    display: flex;
    justify-content: space-between;
    padding: 5px 15px 5px 0;
    border-bottom: 1px solid gainsboro;
    > div:first-child {
      width: 20px;
      color: red;
      font-size: 20px;
      margin: 0 5px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .mv-data {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }
    img {
      width: 50%;
    }
  }
}
</style>