<template>
  <div class="listen">
    <div class="header-nav">
      <navTop :list="nav" @tap-Nav="tabNavTop" />
    </div>
    <br>
    <div>
      <keep-alive>
        <component :is="comName"></component>
      </keep-alive>
    </div>
  </div>
</template>

<script>
//顶部导航
import navTop from "./NavTop.vue";
//新歌速递
import musicNew from "./musicNew.vue";
//视频
import Video from "./Viedo.vue"
export default {
  name: "List",
  props: {},
  components: {
    navTop,
    musicNew,
    Video
  },
  data() {
    return {
      nav: [
        {
          title: "新歌速递",
          active: true,
        },
        {
          title: "视频",
          active: false,
        },
        {
          title: "歌手榜",
          active: false,
        },
        {
          title: "电台主播榜",
          active: false,
        },
      ],
      comName: "musicNew",
    };
  },
  methods: {
    //切换导航
    tabNavTop(i) {
      this.nav.forEach((item) => {
        item.active = false;
      });
      this.nav[i].active = true;
      switch(i){
        case 0 :
          this.comName='musicNew';
          break;
        case 1 :
          this.comName = 'Video';
          break;
        case 2 :
          this.comName = 'singer';
          break;
        case 3 :
          this.comName = "diantai"
      }
    },
  },
};
</script>

<style lang="less" scoped>
.header-nav {
  position: fixed;
  width: 100%;
}
</style>