<template>
  <div class="pocast">
    <div class="header">
      <div class="left">三</div>
      <div>
        <appNav @nav-item="currentIndex" />
      </div>
      <div class="yuying">+</div>
    </div>
    <transition
      name="musicPage"
      :duration="{ enter: 800, leave: 300 }"
      enter-active-class="animate__animated animate__bounceInLeft"
      leave-active-class="animate__animated animate__bounceOutRight"
    >
      <keep-alive>
        <component :is="comName"></component>
      </keep-alive>
    </transition>
  </div>
</template>

<script>
import appNav from "../components/pocastNav.vue";
import Listen from "@/components/Listen.vue";
import Referrals from "@/components/Referrals.vue";
import Sort from "@/components/Sort.vue";
export default {
  name: "Prodcast",
  data() {
    return {
      comName: "Referrals",
      index: 2,
      
    };
  },
  components: {
    appNav,
    Listen,
    Referrals,
    Sort,
  },
  methods: {
    // 导航当前选项的索引值
    currentIndex(i) {
      if (i == this.index) {
        return;
      }
      // 父组件记录索引值 let index = i;
      this.index = i;
      // 根据索引值切换界面
      switch (i) {
        case 0:
          this.comName = "Listen";
          break;

        case 1:
          this.comName = "Referrals";
          break;

        case 2:
          this.comName = "Sort";
          break;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.header {
  width: 100%;
  padding: 5px 10px;
  position: fixed;
  background: white;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  overflow: hidden;
  z-index: 99;
  .left {
    font-size: 20px;
    padding-top: 5px;
  }
  .yuying {
    width: 25px;
    line-height: 40px;
    text-align: center;
    // background: goldenrod;
    font-size: 40px;
    color: red;
  }
}
.pocast {
  overflow: hidden;
  padding-top: 50px;
  padding-bottom: 120px;
}
</style>