<template>
  <div>
    <div class="nav">
      <ul>
        <li :class="index == 0 ? 'active' : ''" >
          <span @click="toggle(0, $event)">听歌</span>
        </li>

        <li :class="index == 1 ? 'active' : ''" ref="act">
          <span @click="toggle(1, $event)">推荐</span>
        </li>

        <li :class="index == 2 ? 'active' : ''">
          <span @click="toggle(2, $event)">分类</span>
        </li>
      </ul>

      <div :style="`left:${left}px;width:${width}px;`" class="line"></div>
    </div>
  </div>
</template>


<script>
export default {
  name: "appleNav",
  data() {
    return {
      index: 1,
      left: 0,
      width: 0,
    };
  },
  methods: {
    toggle(index, event) {
      // console.log(index);
      // console.log(event.target);
      // 设置索引值
      this.index = index;
      // 获取当前点击的元素
      let element = event.target;
      // console.log(element.tagName);//SPAN
      // 判断
      if (element.tagName == "SPAN") {
        // 位置
        let x = element.offsetLeft;
        // 尺寸
        let w = element.offsetWidth;
        // console.log({x,w});
        // 赋值
        this.left = x;
        this.width = w;
        // 传值给父组件(自定义事件类型，数据)
        this.$emit("nav-item", this.index);
      }
    },
  },
  mounted() {
    // 目的：初始化时 获取默认项标签位置、尺寸
    // 调用nexttick方法 DOM更新后
    // console.log(this);
    // $nextTick
    this.$nextTick(() => {
      // 获取refs对象的act标签
      let el = this.$refs.act;
      let element = el.children[0];

      // console.log(el.children);
      this.left = element.offsetLeft;
      this.width = element.offsetWidth;
    });
  },
};
</script>




<style lang="less" scoped>
.nav {
  width: 100%;
  height: 35px;
  background-color: #ffffff;
  position: relative;
  ul {
    width: 100%;
    height: 42px;
    display: flex;
    li {
      flex: 1 0 33.33333%;
      height: 40px;
      font-size: 15px;
      text-align: center;
      line-height: 40px;
      span {
        display: inline-block;
        height: 40px;
        vertical-align: middle;
        padding: 0 5px;
        font-family: Arial, Helvetica, sans-serif;
        border-bottom: 2px solid transparent;
      }
    }
    li.active {
      span {
        color: #f00;
        border-bottom: 2px solid transparent;
      }
    }
  }
  .line {
    width: 100px;
    height: 2px;
    background-color: #f00;
    position: absolute;
    bottom: 0;
    left: 5px;
    transition: left 0.2s;
  }
}
</style>