<template>
  <div class="music-new" ref="header">
    <ul
      class="music-list"
      v-for="(item, index) in musicList[limt]"
      :key="index"
    >
      <li class="music-list-item">
        <img :src="item.album.picUrl" alt="" />
        <div class="item-content">
          <div class="name">{{ item.name }}</div>
          <div class="artist">
            <span v-for="(v, i) in item.artists" :key="i">
              {{ v.name }}
            </span>
          </div>
        </div>
        <div class="toggle">
          <div @click="play(item, index)">播放</div>
        </div>
      </li>
    </ul>
    <div>
      <van-pagination
        v-model="limt"
        :page-count="musicList.length - 1"
        mode="simple"
        @change="backTop"
      />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      type: 7,
      musicList: [{ name: "" }],
      limt: 1,
      text: "",
      flag: false,
      id: "",
      lrc: "",
    };
  },

  methods: {
    //获取新歌速递内容
    async getMusicNew() {
      this.axios
        .get("top/song", { params: { type: this.type } })
        .then((res) => {
          if (res.code == 200) {
            let data = res.data;
            let newArr = [];
            for (let i = 0; i < data.length; i = 10) {
              let arr = data.splice(i, 10);
              this.musicList.push(arr);
              newArr.push(arr);
            }
            newArr.forEach((item) => {
              item.forEach((v, i) => {
                v.isPlayState = false;
              });
              this.musicList = newArr;
            });
          }
        });
    },
    //点击下一页 回到顶部
    backTop() {
      const that = this;
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      document.documentElement.scrollTop = document.body.scrollTop =
        that.scrollTop;
    },
    play(item,index) {
      //获取歌词
      this.getLuric(item.id);
      let i = this.limt;
      //播放与暂停状态的切换
      this.tapPlay(i,index);
      let obj = {};
      //歌词
      obj.lrc = this.lrc;
      //记录当前点击的 音乐id
      this.id = item.id;
      // 歌曲id
      obj.id = item.id;
      //歌曲名字
      obj.name = item.name;
      // 歌手
      obj.artist = "";
      //歌曲播放地址
      obj.url = `https://music.163.com/song/media/outer/url?id=${item.id}.mp3`;
      // 歌曲图片
      obj.cover = item.album.picUrl;
      
      for (let i in item.artists) {
      //获取歌手
        obj.artist += item.artists[i].name + "/";
      }
      //播放状态
      obj.isPlayState = item.isPlayState;
      this.$store.commit("tapMusicId", obj);
    },
    //歌词获取
    getLuric(id) {
      if (id == this.id) {
        return;
      }
      this.axios
        .get("lyric", {
          params: {
            id: id,
          },
        })
        .then((res) => {
          if (res.code == 200) {
            this.lrc = res.lrc.lyric;
            console.log(this.lrc);
            console.log("lrc ==>", this.musicList[0][0].isPlayState);
          }
        });
    },
    //播放状态的切换
    tapPlay(index, i) {
      let arr = this.musicList;
      if (arr[0].name == "") {
        arr.splice(0, 1);
      }
      let flag = arr[index][i].isPlayState;
      //播放状态切换
      arr.forEach((v, i) => {
        v.forEach((a) => {
          //都变为false
          a.isPlayState = false;
        });
      });
      this.flag = !this.flag;
      //当前点击的 变为 false || true
      arr[index][i].isPlayState = !flag;
      //重新赋值
      this.musicList = arr;
      console.log("tapPlay ==>", this.musicList[index][i].isPlayState);
    },
  },
  created() {
    this.getMusicNew();
    this.flag = true;
  },
};
</script>

<style lang="less" scoped>
.music-new {
  margin-top: 34px;
}
.music-list-item {
  height: 60px;
  padding: 5px;
  display: flex;
  justify-content: space-between;
  background: white;
  border-bottom: 1px solid gainsboro;
  .item-content {
    width: 66%;
    padding-top: 10px;
    text-align: center;
    div {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
  .name {
    font-weight: bold;
    font-size: 16px;
    color: red;
    overflow: hidden;
  }
  .artist {
    color: gray;
  }
  .toggle {
    width: 10%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    > div {
      padding: 5px;
      border-radius: 20px;
      border: 1px solid black;
      &:active {
        transition: all 0.5s;
        background: gray;
        transform: scale(2, 1);
      }
    }
  }

  img {
    width: 20%;
  }
}
</style>