<template>
  <div class="nav-title">
    <ul class="nav-list">
      <li
        :class="item.active ? 'nav-item active' : 'nav-item'"
        v-for="(item, index) in list"
        :key="index"
        @click="tabActive(index)"
      >
        <h5>{{ item.title }}</h5>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    list: Array,
  },
  methods:{
    tabActive(i){
      this.$emit('tap-Nav',i)
    }
  }
};
</script>

<style lang="less" scoped>
.nav-title {
  padding: 10px 5px;
  background: white;
  width: 100%;
  .nav-list {
    height: 100%;
    display: flex;
    justify-content: space-around;
    .nav-item {
      padding: 5px;
    }
    .nav-item.active {
      background: red;
      color: white;
      border-radius: 20px;
    }
  }
}
</style>